.aot {
  max-width: 1920px;
  background-color: rgba(9, 9, 9, 0.823);
  height: calc(100vh - 100px);
  box-sizing: border-box;
}

.say_hello_text_container {
  width: 72%;
  display: flex;
  flex-direction: column;
  color: #4AD592;


  #span {
    line-height: 0.8;
    margin-top: 0;
    margin-bottom: 0;
    margin-left: 50px;
    gap: 0;
    font-size: 128px;
    font-weight: bold;
    font-family: 'Baloo 2';
    text-shadow: 1px 1px 4px rgb(4, 4, 4);

  }

  #span1 {
    margin-left: 53px;
    margin-top: 80px;
    margin-bottom: 0;
    font-size: 61px;
    text-shadow: 1px 1px 4px rgb(4, 4, 4);
    font-family: 'Montserrat';
    text-wrap: nowrap;
  }
}

.spanposition {
  display: flex;
  flex-direction: column;
  margin-right: 590px;
}

.say_hello_text_container_with_img {
  display: flex;

  img {
    z-index: 9;
    height: 846px;


  }

}


.quick_link_container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-right: 160px;
  margin-top: 200px;
  justify-content: center;

  img {
    height: 52px;
    width: auto;
    margin-top: 24px;

    &:hover {
      margin-right: 6px;
      -webkit-filter: invert(1);
      filter: invert(1);
    }
  }

}

#social_logo_3 {
  margin-top: 26px;
  width: 38px;
  height: 40px;

  &:hover {
    margin-right: 6px;
  }
}



.preview_stripe {
  margin-top: 200px;
  margin-left: 540px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  height: 298px;


  a {
    margin-left: 28px;
    box-shadow: inset 0 0 0 0 #4AD592;
    color: #4AD592;

    transition: color .3s ease-in-out, box-shadow .3s ease-in-out;
    text-decoration: none;
    cursor: pointer;
    text-shadow: 1px 1px 4px rgb(4, 4, 4);
    padding: 3px;
    font-size: 18px;
    font-weight: bold;
    font-family: 'Montserrat';

    &:hover {
      font-weight: bold;
      color: #fff;
      box-shadow: inset 200px 0 0 0 #4AD592;
      ;
      border-radius: 6px;

    }
  }

  img {
    height: 300px;
    width: 1px;
  }
}

.quick_link_preview_stripe_container {
  display: flex;
  justify-content: space-between;

}
@media (max-width:1669px) {
  .spanposition{
    margin-right: 0;
  }
}

@media (max-width: 1586px) {
  #span {
    font-size: 112px;
  }

  #span1 {
    font-size: 55px;
  }
}


@media (max-width: 1509px) {
  #span {
    font-size: 96px;
  }

  #span1 {
    font-size: 44px;
  }

  .preview_stripe {
    margin-top: 240px;
  }
}
@media (max-width: 1450px) {
  .preview_stripe{
    display: none;
  }
  .quick_link_container{
    display: none;
  }
}

@media (max-width:1437px) {
.say_hello_text_container{
  width: 70%;
}
.say_hello_text_container_with_img img {
  margin-left: 44px;
}
}

@media(max-width:1350px) {

  .spanposition {
    display: none;
  }


  #port_main_img {
    height: 745px;
    width: none;
    position: absolute;
    bottom: 0;
 
  }

  .say_hello_text_container_with_img img {
    margin-left: 0px;
  }

  
}


@media(max-width:1265px) {
  #port_main_img {
    height: 690px;
    width:350px;

    margin-left: 76px;
    position: absolute;
    bottom: 0;
  }
}



@media(max-width:798px) {


  .quick_link_container {
    display: none;
  }
}
@media(max-width:520px) {

  .say_hello_text_container_with_img img {
    margin-left: 0px;
  }


}

@media(max-width:460px) {
  #port_main_img {
 left: -100px;
max-height: 72%;

  }


}